// src/components/pie-chart/pie-chart.tsx
import { Component, Prop, h, Element } from '@stencil/core';
import * as d3 from 'd3';

@Component({
  tag: 'pie-chart',
  styleUrl: 'pie-chart.css',
  shadow: true,
})
export class PieChart {
  @Element() el!: HTMLElement;

  @Prop() data: { label: string; value: number }[] = [];

  componentDidLoad() {
    this.drawChart();
  }

  drawChart() {
    const svgEl = this.el.shadowRoot?.querySelector('#pie-chart') as SVGSVGElement | null;
    if (!svgEl || !this.data.length) return;

    const width = 300;
    const height = 300;
    const radius = Math.min(width, height) / 2;

    // Clear previous chart content
    d3.select(svgEl).selectAll('*').remove();

    const svg = d3
      .select(svgEl)
      .attr('width', width)
      .attr('height', height)
      .append('g')
      .attr('transform', `translate(${width / 2}, ${height / 2})`);

    const color = d3.scaleOrdinal(d3.schemeTableau10);
    const pie = d3.pie<{ label: string; value: number }>().value((d) => d.value);
    const arc = d3.arc<d3.PieArcDatum<{ label: string; value: number }>>()
      .innerRadius(0)
      .outerRadius(radius);

    
    let tooltip = this.el.shadowRoot?.querySelector('.tooltip') as HTMLDivElement | null;
    if (!tooltip && this.el.shadowRoot) {
      tooltip = document.createElement('div');
      tooltip.className = 'tooltip';
      this.el.shadowRoot.appendChild(tooltip);
    }

    const arcs = svg.selectAll('arc').data(pie(this.data)).enter().append('g');

    arcs
      .append('path')
      .attr('d', arc)
      .attr('fill', (_, i) => color(i.toString()))
      .on('mouseover', (_, d) => {
        if (tooltip) {
          tooltip.textContent = `${d.data.label}: ${d.data.value}`;
          tooltip.style.opacity = '1';
        }
      })
      .on('mousemove', (event) => {
        if (tooltip) {
          tooltip.style.left = `${event.offsetX + 10}px`;
          tooltip.style.top = `${event.offsetY + 10}px`;
        }
      })
      .on('mouseout', () => {
        if (tooltip) {
          tooltip.style.opacity = '0';
        }
      });

    arcs
      .append('text')
      .attr('transform', (d) => `translate(${arc.centroid(d)})`)
      .attr('text-anchor', 'middle')
      .attr('font-size', '12px')
      .attr('fill', 'white')
      .text((d) => d.data.label);
  }

  render() {
    return (
      <>
        <svg id="pie-chart"></svg>
        {/* Tooltip container lives outside the SVG */}
        <div class="tooltip" style={{ position: 'absolute' }}></div>
      </>
    );
  }
}
